 *
 * Replace them with your own styles or remove the file completely.
 */
.wptravel-block-trip-outline{
    .col {
        display: flex;
        flex-wrap: wrap;
        .tc-heading {
            position: relative;
            width: calc(50% - 1px);
            padding: 0px 50px 15px 50px;
            text-align: right;
            h4 {
                font-size: 22px;
                text-transform: uppercase;
                margin-top: 0;
                margin-bottom: 0px;
            }
            h3:last-of-type {
                font-style: italic;
                font-weight: 100;
            }
        }
        h3 {
            margin-top: 0;
            font-size: 15px;
            font-weight: bold;
            margin-bottom: 3px;
            flex-basis: 100%;
        }
    }
    .timeline-contents .col:last-of-type .tc-content {
        padding-bottom: 0;
    }
    .tc-content.right {
        border-left: 2px solid;
    }
    .tc-content {
        position: relative;
        width: calc(50% - 1px);
        padding: 0 50px 15px 50px;
    }
    .itenary .timeline-contents .tc-content img {
        transform: translateY(8px);
    }
    .timeline-contents .col:last-of-type .tc-content p {
        margin-bottom: 0;
    }
    .itenary .image {
        height: 25px;
        width: 25px;
        left: -13px;
        background: #8b8b8b;
        top: 0;
        border-radius: 50%;
        border: 2px solid;
        position: absolute;
    }
}

.timeline-contents h2 {
    text-align: center;
}
